<script>
	export default {
		data() {
			return {
        value:'',
        userId:localStorage.getItem('userId'),
        search:'',
        goodsListOne: [],
        goodsList:[],
        cityName:localStorage.getItem('cityName')||'南京市',
        loading: false,
        finished: false,
        pageNum: 1,
			}
		},
		mounted() {
      this.getGoodsList();
		},
		methods: {
      getGoodsList1(){
        this.$router.push({path:'goodsSearch',query:{value:this.value}});
      },
      getGoodsList(){
        this.loading = true;
        let $this = this;
        let obj = {
          userid: $this.userId,
          search: this.value,
          pageNum: $this.pageNum
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/gettuijianlist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            // debugger;
            if(rpdata.data.data.data.length>0){
              for(let i = 0;i<rpdata.data.data.data.length;i++){
                // console.log(rpdata.data.data.data[i].sameimg.split(',')[0]);
                if(rpdata.data.data.data[i].sameimg){
                  rpdata.data.data.data[i].goodsImg = rpdata.data.data.data[i].sameimg.split(',')[0];
                }
              }
              $this.goodsList = $this.goodsList.concat(rpdata.data.data.data);
              $this.pageNum++;
              $this.loading = false;
            }else if(rpdata.data.data.data.length==0){
              $this.finished = true;
              $this.loading = false;
            }
          }
        })
        .catch(function(err) {})
      },
      toRentalList(){
        this.$router.push('RentalList');
      },
      toCityService(){
        this.$router.push('cityService');
      },
      toBuyAndSell(){
        this.$router.push('buyAndSell');
      },
      toCityDispatching(){
        this.$router.push('cityDispatching');
      },
      toJobs(){
        this.$router.push('Jobs');
      },
      goodsClick(dataMap){
        if(dataMap.type==1){//维修
          this.$store.commit('setServiceInfo', dataMap);
          this.$router.push('serviceInfo');
        }else if(dataMap.type==2){//买卖
          this.$store.commit('setRoomInfo', dataMap);
          this.$router.push('roomInfo');
        }else if(dataMap.type==3){//租赁
          this.$store.commit('setRoomInfo', dataMap);
          this.$router.push('roomInfo');
        }else if(dataMap.type==4){//快递
          this.$store.commit('setDispatchingInfo', dataMap);
          this.$router.push('dispatchingInfo');
        }else if(dataMap.type==5){//求职
          this.$store.commit('setResumeInfo', dataMap);
          this.$router.push('resume');
        }else if(dataMap.type==6){//招聘
          this.$store.commit('setJobInfo', dataMap);
          this.$router.push('JobInfo');
        }
      },
		}
	}
</script>

